import back from '@/components/back'

export default {
    components:{
        back
    },
    data(){
        return {
            id: this.$route.query.id,
            ListDetails: [],
            Diagnoses: null,
            user: this.$store.state.userInfo,
        }
    },
    created(){
        if(this.id){
            this.$post('Prescription/Prescription/GetPrescriptionOftenUsed',{
                Id: this.id
            }).then(o => {
                this.Diagnoses = o.Diagnoses
                const { cMed,upList } = this.$store.state
                if(upList.length){
                    if(cMed){
                        upList.push(cMed)
                        this.$store.dispatch('pushcMed', null)
                    }
                    this.ListDetails = upList
                }else{
                    this.ListDetails = o.ListDetails
                }
            })
        }
        else{
            const { cMed, upList, Diagnoses, saveFlag } = this.$store.state
            if(cMed && saveFlag == 1){
                upList.push(cMed)
                this.$store.dispatch('pushcMed', null)
            }
            this.ListDetails = upList
            this.Diagnoses = Diagnoses
        }
    },
    beforeDestroy(){
        this.$store.dispatch('pushupList', this.ListDetails)
        this.$store.dispatch('pushDiagnoses', this.Diagnoses)
    },
    methods:{
        pop(idx){
            this.ListDetails.splice(idx, 1)
        },
        modify(idx){
            this.$store.dispatch('pushcMed', this.ListDetails[idx])
            this.$router.push(`/consumption?idx=${idx}&&back=-1`)
        },
        toUm(){
            this.$store.dispatch('pushisUp', 0)
            this.$router.push('/pharmacy?from=up')
        },
        back(){
            this.ListDetails = []
            this.$router.back()
        },
        save(){
            if(!this.Diagnoses){
                return this.$toast('请填写临床诊断')
            }
            if(!this.ListDetails){
                return this.$toast('请选择药品')
            }
            const list = []
            this.ListDetails.forEach(v => {
                const obj = {}
                obj.MedicationId = v.Id
                obj.UsageTimeId = v.UsageTimeId
                obj.UsageTime = v.UsageTime
                obj.UsageMethodId = v.UsageMethodId
                obj.UsageMethod = v.UsageMethod
                obj.PackageUnitId = v.PackageUnitId
                obj.PackageUnit = v.PackageUnit
                obj.DayDosageId = v.DayDosageId
                obj.DayDosage = v.DayDosage
                obj.Dosage = v.Dosage
                obj.DosageUnitId = v.DosageUnitId
                obj.DosageUnit = v.DosageUnit
                obj.Quantity = v.Quantity
                obj.Remark = v.Remark
                obj.DosageDaysId= v.DosageDaysId,
                obj.DosageDays= v.DosageDays
                list.push(obj)
            })
            this.$post('Prescription/Prescription/SavePrescriptionOftenUsed', this.$pick({
                id: this.$route.query.id,
                DoctorId: this.user.Data.User.DoctorId,
                OrgId: this.user.Data.User.OrgId,
                Diagnoses: this.Diagnoses,
                ListDetails: list
            })).then(o => {
                this.ListDetails = []
                this.Diagnoses = ''
                this.$store.dispatch('pushisUp', 1)
                this.$router.back()
            })
        }
    }
}



